import axios from 'axios';
export const ForecastTypePaths = {
    ["cost" /* cost */]: 'forecasts/aws/costs/',
};
export function runForecast(forecastType, query) {
    const path = ForecastTypePaths[forecastType];
    return axios.get(`${path}?${query}`);
}
